body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.port {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.port:hover:not(.disabled) {
    cursor: pointer;
}

.port.disabled {
    cursor: not-allowed;
    opacity: 0.5;
}
.rack {
    position: relative;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    background-size: contain;
    background-image: url(/modularsynth.js/static/media/rack_bg.cd50a535.svg);
    height: 370px;
    overflow-x: scroll;
}
.module-panel {
    display: flex;
    flex-direction: column;
    background: #E8E8E8;
    border: black 0.2px solid;
    position: absolute;
    height: 100%;
}

.panel_content {
    flex: 1 1;
}

.btn_delete_module {
    visibility: hidden;
    color: gray;
    cursor: pointer;
}

.btn_delete_module:hover {
    color: black;
}

.module-panel:hover .btn_delete_module {
    visibility: visible;
}

.panel_edge {
    display: flex;
    justify-content: space-between;
    margin: -2px 0;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

